import React, {Component} from "react";
import MultiCartItemTooltip from "./MultiCartItemTooltip";
import OraStockLevelStatusComponent from "eshop/components/OraStockLevelStatusComponent";
import OnlineUtils from "eshop/utils/OnlineUtils";

export default class MultiCartItemAttributeComponent extends Component {

    constructor (props) {
        super(props);
    }

    getPartsOfPrice(price) {
        let pricePartsArray,
            convertedPrice = [];

        if (typeof price === 'undefined')
            return [null, null];

        if (typeof price === 'number')
            price = price.toFixed(2);

        pricePartsArray = price.match(/^(\d+)?((,|.)(\d{2}))?$/);

        if (pricePartsArray) {
            convertedPrice.push(pricePartsArray[1]);
            convertedPrice.push( typeof pricePartsArray[4] !== 'undefined' ? pricePartsArray[4] : '00' );
        }

        return convertedPrice;
    }

    transformMsisdn(number) {
        return number.replace(/^(\d{3})(\d{3})(\d{3})$/, function () {
            return arguments[1] + '\xa0' + arguments[2] + '\xa0' + arguments[3];
        })
    }

    getPriceSpelling(price) {
        if (price.monthTo && ( !price.monthFrom || price.monthFrom === 1 )) {
            if (price.monthTo === 1) {
                return 'przez pierwszy miesiąc';
            } else if (price.monthTo > 1 && price.monthTo < 5) {
                return 'przez pierwsze ' + price.monthTo + ' miesiące';
            } else if (price.monthTo >= 5) {
                return 'przez pierwsze ' + price.monthTo + ' miesięcy';
            }
            return 'do ' + price.monthTo + '. miesiąca';
        } else if (!price.monthTo && element.monthFrom) {
            return 'od ' + price.monthFrom + '. miesiąca';
        } else {
            return 'od ' + price.monthFrom + '. do ' + price.monthTo + '. miesiąca';
        }
    }

    getPricesTooltip(allPrices) {
        let prices = allPrices && allPrices.length > 1 && allPrices.map((element, idx) => {
            return (
                <li key={ idx }>{ this.getPriceSpelling(element) }: { element.price }&nbsp;{ element.currency }</li>
            )
        });

        if (prices) {
            return (
                <MultiCartItemTooltip>
                    <div>Opłaty miesięczne:</div>
                    <ul>
                        { prices }
                    </ul>
                </MultiCartItemTooltip>
            )
        }
    }

    getHighestMonthlyPrice(monthlyPrices) {
        return monthlyPrices
            .slice()
            .sort((a, b) => {return a.price - b.price})
            .pop();
    }

    getCrossedOutPrice(pricePresentationMode, monthlyOldPrice, monthlyPrices) {
        switch (pricePresentationMode) {
            case 'DISCOUNTED_PRICE':
                if (monthlyOldPrice) {
                    return <span>{ this.getPartsOfPrice(monthlyOldPrice.price).join(',') }&nbsp;{ monthlyOldPrice.currency }</span>;
                }
                break;
            case 'HIGHEST':
                if (monthlyPrices) {
                    return <span>{ this.getPartsOfPrice(this.getHighestMonthlyPrice(monthlyPrices)).join(',') }</span>
                }
                break;
        }
    }

    renderVoidDesktop() {
        return (
            [<span className="h4 u-no-spacing" aria-hidden="true">&mdash;</span>
            , <span className="u-acc-hide">brak</span>
            ]
        )
    }
    renderPriceDesktop(price, stockLevelStatus = null, className = null) {
        return (
            <div className="u-inline">
                <span className="u-small-hide u-medium-hide opl-price-v2 opl-price-v2--s">
                    <span className="opl-price-v2__part">
                        <span className="opl-price-v2__whole">{ this.getPartsOfPrice(price.price)[0] }</span>
                    </span>
                    <span className="opl-price-v2__part">
                        <span className="opl-price-v2__separator">,</span>
                        <span className="opl-price-v2__decimal">{this.getPartsOfPrice(price.price)[1]}</span>
                        <span className="opl-price-v2__suffix">{price.currency}</span>
                    </span>
                </span>
                <span className="u-small-hide u-large-hide opl-price-v2 opl-price-v2--xs">
                    <span className="opl-price-v2__part">
                        <span className="opl-price-v2__whole">{ this.getPartsOfPrice(price.price)[0] }</span>
                    </span>
                    <span className="opl-price-v2__part">
                        <span className="opl-price-v2__separator">,</span>
                        <span className="opl-price-v2__decimal">{this.getPartsOfPrice(price.price)[1]}</span>
                        <span className="opl-price-v2__suffix">{price.currency}</span>
                    </span>
                </span>
            </div>
        )
    }

    renderPrice(className = null) {
        return this.props.monthlyPricePresentation.main ? this.renderPriceDesktop(this.props.monthlyPricePresentation.main, this.props.stockLevelStatus, className ) : this.renderVoidDesktop();
    }

    prepareCrossedOneTimePrice(simFreeOneTimePrice) {
        return (
            Math.floor(simFreeOneTimePrice.priceWithoutVouchers * 100) / 100 > simFreeOneTimePrice.price &&
            <del className="h4 u-font-small g-gray5-c u-inline-block">
                {OnlineUtils.formatPrice(simFreeOneTimePrice.priceWithoutVouchers) + " " + simFreeOneTimePrice.currency}
            </del>
        );
    }
}